import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-check-section',
  styleUrl: 'orderCheckSection.css'
})
export class OrderCheckSection {
    @Element() root: HTMLElement;

    @Prop() label: string;
    @Prop() name: string;
    @Prop({mutable: true, reflect: true}) active: boolean = false;


    ChangeHandler() {
        const input = this.root.querySelector(`div > ks-input-check input`) as HTMLInputElement;
        this.active = input.checked;

        const inputs = this.root.querySelectorAll("ks-input-text");
        inputs.forEach(input => {
            input.message = "";
            input.disabled = !this.active
        });

        const selects = this.root.querySelectorAll("ks-input-select");
        selects.forEach(input => {
            input.disabled = !this.active
        });

        const buttons = this.root.querySelectorAll("ks-button");
        buttons.forEach(input => {
            input.disabled = !this.active
        });
    }

    componentDidLoad(){
        this.ChangeHandler();
    }

    render() {
        return (
            <div>
                <div class="input">
                    <ks-input-check class="enable" onChange={()=>this.ChangeHandler()} ariaLabel='Aktywuj sekcjÄ™' />
                    <h1>{this.label}</h1>
                </div>

                {!this.active && <input type="hidden" name={this.name} value="1" />}

                <div class="section">
                    <slot />
                    {!this.active && <div class="overlay" />}
                </div>
                
            </div>
        )
    }
}