ks-order-check-section .enable span {
    font-size: 22px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}

ks-order-check-section .input {
    display: flex;
    align-items: flex-end;
}

ks-order-check-section .input > * {
    margin-bottom: 12px;
}

ks-order-check-section .section {
    position: relative;
}

ks-order-check-section .overlay {
    position: absolute;
    inset: 0;
    background: white;
    padding: 30px 30px;
    opacity: 0.8;
}

@media (max-width: 959px) {
    ks-order-check-section:not([active]) .section {
        display: none;
    }
}